import React from 'react';
import s from './s.module.scss';
import cn from 'classnames';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import imagePath from './images/man.png';
import Image from "react-bootstrap/Image";
import userIconPath from './images/icon_user.png';
import emailIconPath from './images/icon_email.png';
import CustomButton from "../../Button";
import {Form, Text} from 'informed';
import Container from "react-bootstrap/Container";

const OpenAccount = ({onSubmit}) => (
  <>
    {/*<Row className={s.brief}>*/}
    {/*  <Col className={s.text} md={12}>*/}
    {/*    Lorem ipsum dolor sit amet, consectetur adipiscing elit. Suspendisse est purus, dictum eu urna ut, aliquam consectetur odio. Ut pellentesque vitae purus lobortis lacinia. Nam at nisi tempor, euismod augue sit amet.*/}
    {/*  </Col>*/}
    {/*</Row>*/}
    <Row className={s.main}>
      <Col className={s.imageContainer} sm={12} md={6}>
        <Image className={s.image} src={imagePath}/>
      </Col>
      <Col sm={12} md={6}>
        <Form onSubmit={onSubmit} className={s.inputs}>
          <CustomInput field={'address'} placeholder={'Адрес'}/>
          <CustomInput field={'phone'} placeholder={'Телефон'}/>
          <CustomInput field={'email'} placeholder={'Email'} type={'email'}/>
          <CustomButton
            containerClassName={s.buttonContainer}
            className={s.button}
            borderColor={'#1ac0ea'}
            color={'black'}
            title={'ОТКРЫТЬ СЧЕТ'}
          />
        </Form>
      </Col>
    </Row>
  </>
);

export const CustomInput = ({iconPath, placeholder, field, ...props}) => (
  <div className={s.input}>
    {iconPath && <div className={s.icon}><Image src={iconPath}/></div>}
    <div className={cn(s.inputElementContainer, 'px-4')}>
      <Text className={s.element} placeholder={placeholder} field={field} required {...props}/>
    </div>
  </div>
);

export default OpenAccount;