import React, {useEffect, useState} from 'react';
import Landing, {Loading} from "./Landing";
import '../common/global.css';
import {HashRouter as Router, Redirect, Route} from 'react-router-dom';
import Dashboard from "./Dashboard";
import getParameterByName from "../utils/getParameterByName";
import NetworkContext, {useNetwork} from "../contexts/NetworkContext";
import DataContext, {useApplicationData} from "../contexts/DataContext";

const MINIMAL_LOADING_TIME = 1000;

function App() {
  const [isDataLoaded, setIsDataLoaded] = useState(false);

  const {isNetworkReady, network} = useNetwork();
  const appData = useApplicationData();

  const {user, products} = appData;

  useEffect(() => {
    if (!network.loggedIn) network.onStartUp();
    if (!network.loggedIn) {
       products.load().then(() => setIsDataLoaded(true));
    } else {
      Promise.all([
        products.load(),
        user.load(),
      ]).then(() => setIsDataLoaded(true));
    }
  }, [network.loggedIn]);

  const [landingTabIndex, setLandingTabIndex] = useState(0);
  const [isReadyToPassLoadingScreen, setIsReadyToPassLoadingScreen] = useState(false);

  useEffect(() => {
    if (getParameterByName('redirect') === 'login') setLandingTabIndex(4);
    setTimeout(() => setIsReadyToPassLoadingScreen(true), MINIMAL_LOADING_TIME);
  }, []);

  const redirect =
    (network.loggedIn === true && <Redirect to={'/lk'}/>) ||
    (network.loggedIn === false && <Redirect to={'/'}/>);

  console.log(isDataLoaded, isNetworkReady, isReadyToPassLoadingScreen);

  return !isDataLoaded || !isNetworkReady || !isReadyToPassLoadingScreen ?
    <Loading/>
    : (
    <NetworkContext.Provider value={network}>
      <DataContext.Provider value={appData}>
        <Router>
          <Route
            exact
            path={'/'}
            component={() => (
              <Landing
                defaultTabIndex={landingTabIndex}
              />
            )}
          />
          <PrivateRoute
            accessed={network.loggedIn}
            path={'/lk'}
            component={Dashboard}
          />
          {redirect}
        </Router>
      </DataContext.Provider>
    </NetworkContext.Provider>
  );
}

const PrivateRoute = ({component: Component, accessed, ...rest}) => (
  <Route render={props => (
    accessed ?
      <Component {...props}/>
      :
      <Redirect to={'/'}/>
  )} {...rest}/>
);

export default App;
