import React, {useState} from 'react';
import s from './s.module.scss';
import cn from 'classnames';
import styles from "../../style.module.css";
import Image from "react-bootstrap/Image";
import aboutImagePath from "../../images/about_image.png";
import CustomButton from "../../Button";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import {Collapse} from "react-collapse";
import turkeyFlag from './images/turkey.PNG';
import kazakhstanFlag from './images/kazakhstan.PNG';
import netherlandsFlag from './images/netherlands.PNG';
import singaporeFlag from './images/singapore.PNG';

const About = () => {
  const [showMore, setShowMore] = useState(false);

  return (
    <>
      <Row className={s.infoContainer}>
        <Col sm={12} md={8} className={s.text}>
          «НАШ ФОНД» - это команда трейдеров, аналитиков и юристов, с богатым опытом работы в коммерческих и
          некоммерческих организациях, а также государственных финансовых институтах. Наши эксперты, обладают нужными
          качествами, опытом и знаниями в своей области, для решения любых задач. Наша команда более 5-ти лет успешно
          развивает инвестиционные портфели и создает многоуровневые системы торговли на рынках ценных бумаг. Более 1300
          инвесторов работают с нами на протяжении многих лет, что делает нашу команду нацеленной на долгосрочное
          сотрудничество с каждым инвестором «НАШЕГО ФОНДА». Прозрачность торгов и максимальная доступность к средствам,
          даст вам уверенность в завтрашнем дне! Мы покажем Вам как в команде из 300 специалистов можно добиться успеха
          там, где это удается не каждому.
        </Col>
        <Col sm={12} md={4} className={s.imageContainer}>
          <Image className={s.image} src={aboutImagePath}/>
        </Col>
      </Row>
      <Collapse isOpened={showMore}><Additional/></Collapse>
      <CustomButton
        containerClassName={s.button}
        title={showMore ? 'СКРЫТЬ' : 'УЗНАТЬ ЕЩЕ'}
        color={'#1ac0ea'}
        borderColor={'#1ac0ea'}
        onClick={() => setShowMore(_ => !_)}
      />
    </>
  );
};

const Additional = () => (
  <Row className={s.additional}>
    <Title>НАШИ СПЕЦИАЛИСТЫ</Title>
    <SimpleText>Мы приверженцы идеи что успех на финансовых рынках достигается не только при получении экономического образования. Успешный опыт работы, управления, как своим капиталом, так и доверенными средствами, является основой для начала работы в нашей команде.</SimpleText>
    <SimpleText>С 2017-го года, торговля на рынке стала для нас площадкой для демонстрации своего профессионализма и богатых навыков.</SimpleText>

    <Title>Вот некоторые из наших результатов:</Title>
    <ItemText>Первое Место в номинации «Best Managed Account» на международном конкурсе инвестиционных фондов «WeAreFound – 2017»</ItemText>
    <ItemText>Второе Командное Место в номинации «Result - Conservative Strategy» на международном конкурсе инвестиционных фондов «WeAreFound – 2017»</ItemText>

    <CountryTitle text={'Ankara, Republic Of Turkey'} iconSrc={turkeyFlag}/>
    <ItemText>Первое Место в номинации «Best Long-Term Strategy» на международном форуме Хедж-фондов «B invest» - 2017</ItemText>

    <CountryTitle text={'Utrecht, Netherlands'} iconSrc={netherlandsFlag}/>
    <ItemText>Первое Место в номинации «Best Hedge Strategy» на международном конкурсе брокеров «Best Trade Execution Brokers» - 2017</ItemText>

    <CountryTitle text={'Singapore, Republic Of Singapore'} iconSrc={singaporeFlag}/>
    <ItemText>«Наш Фонд» номинирован на премию «Лучший Крипто Брокер» на международном форуме «Invest In Almaty - 2018»</ItemText>

    <CountryTitle text={'Almaty, Republic Of Kazakhstan'} iconSrc={kazakhstanFlag}/>
    <ItemText>«Наш Фонд» в лице сотрудника - Андрей Белоусов, Елена Малкина, Сергей Хан, номинирован на премию «Лучший Трейдер Года» на международном форуме «Invest In Almaty - 2018».</ItemText>
    <ItemText>«Наш Фонд» и сотрудники фонда выражают искреннюю благодарность Андрею Белоусову, Елене Малкиной, Сергей Хану, за заслуги перед компанией и командой. Мы желаем вам успеха в покорении экстра высоких вершин.</ItemText>

    <SimpleText/>

    <SimpleText>С 2017-го года, более 2000 Инвесторов успешно продолжают работу с нами.</SimpleText>
    <SimpleText>Более 2000 инвесторов считают «Наш Фонд» более рентабельным вложением, по сравнению с банковскими депозитами.</SimpleText>
    <SimpleText>Более 700 инвесторов признают что, получаемые доходы (%) от вложений в «Наш Фонд», превышают доходы (%) получаемые в их собственном бизнесе.</SimpleText>
    <SimpleText>Более 130 инвесторов заключили контракты на долгосрочное развитие портфеля в рамках проекта ВРС «Все Ради Семьи», сроком на 3 года и более.</SimpleText>
    <SimpleText>Спасибо что вы с нами! Мы продолжим удивлять Вас наилучшими результатами.</SimpleText>

    <Title>ХОТИТЕ СТАТЬ ЧАСТЬЮ НАШЕЙ КОМАНДЫ?</Title>
    <SimpleText>Еженедельно мы получаем рекомендации специалистов в области торговли на рынке ценных бумаг, крипто валют и рынке Форекс. Мы приверженцы идеи что успех на финансовых рынках достигается не только при получении экономического образования. Статистика успешной, многолетней работы, управления, как своим капиталом, так и доверенными средствами, является основой для начала работы в нашей команде.</SimpleText>
    <SimpleText><span className={s.bull}>&bull;</span>Вы выпускник высшей школы экономики?</SimpleText>
    <SimpleText><span className={s.bull}>&bull;</span>Вы торгуете на рынках более 10-ти лет?</SimpleText>
    <SimpleText><span className={s.bull}>&bull;</span>Вы разработали уникальную стратегию, которая на протяжении многих лет приносит вам стабильную прибыль?</SimpleText>
    <SimpleText><span className={s.bull}>&bull;</span>Вы готовы показать историю ваших успешных торгов за последние 5 лет?</SimpleText>

    <SimpleText>Если на все эти вопросы вы ответили «ДА», тогда возможно ваше место в нашей команде!</SimpleText>
    <SimpleText className={s.bold}>Отправьте свое резюме на наш email адрес.</SimpleText>
  </Row>
);

export const Title = ({children, className, style}) => (
  <div className={cn(s.title, className)} style={style}>{typeof children === 'string' ? children.toUpperCase() : children}</div>
);

const CountryTitle = ({text, iconSrc}) => (
  <div className={s.countryTitle}>
    <div className={s.name}>{text}</div>
    <Image className={s.icon} src={iconSrc} width={32}/>
  </div>
);

export const ItemText = ({children, className}) => (
  <div className={cn(s.countryText, className)}><span className={s.bull}>&bull;</span>{children}</div>
);
export const SimpleText = ({children, className}) => (
  <div className={cn(s.simpleText, className)}>{children}</div>
);

export default About;
