import React, {useState} from 'react';
import s from './s.module.scss';
import cn from 'classnames';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import {Form, Text, TextArea} from "informed";
import Select from 'react-select';
import TouchableOpacity from "../../../../common/core/TouchableOpacity";
import arrowIconPath from './images/icon_go.png';
import Image from "react-bootstrap/Image";
import placeIconPath from './images/icon_place.png';
import phoneIconPath from './images/icon_phone.png';
import emailIconPath from './images/icon_email.png';
import * as firebase from "firebase";
import Modal from "../../../../common/core/Modal";

const reactSelectStyles = {
  container: (provided) => ({
    ...provided,
    width: '100%',
  }),
  control: (provided) => ({
    ...provided,
    borderRadius: 0,
    paddingLeft: '8px',
    height: '2.5rem',
    margin: '0.25rem',
  }),
  placeholder: (provided) => ({
    ...provided,
    color: '#333333',
    fontFamily: "'Montserrat', sans-serif",
    fontWeight: 500,
    fontSize: '0.9rem',
  }),
};

const Contacts = () => {

  const [modalOpen, setModalOpen] = useState(false);

  const sendContactMailToAdmin = async (formState) => {
    const send = firebase.functions().httpsCallable('sendContactMailToAdmin');
    setModalOpen(true);
    await send(formState);
  };

  return (
    <Row className={s.container}>

      <Modal
        isOpen={modalOpen}
        onClose={() => setModalOpen(false)}
        className={s.modal}
      >
        Ваше сообщение было успешно отправлено.
      </Modal>

      <Col className={s.formContainer} xs={10} md={7}>
        <Form onSubmit={sendContactMailToAdmin}>
          <Row className={s.form}>
            <Col md={12}>
              <Text className={s.input} field={'title'} placeholder={'Заголовок'} required/>
            </Col>
            <Col md={12}>
              <TextArea className={s.area} field={'text'} placeholder={'Ваш текст'} required/>
            </Col>
            <Col md={4}>
              <Text className={s.input} field={'name'} placeholder={'Ваше имя'} required/>
            </Col>
            <Col md={8}>
              <Text className={cn(s.input, s.email)} field={'email'} placeholder={'Ваш email'} type={'email'} required/>
              <TouchableOpacity onClick={() => {}} className={s.button}>
                <button style={{width: '100%', height: '100%', backgroundColor: 'transparent', border: 'none', outline: 'none', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                  <Image src={arrowIconPath}/>
                </button>
              </TouchableOpacity>
            </Col>
          </Row>
        </Form>

      </Col>

      <Col className={s.info} xs={10} md={5}>

        <div className={cn(s.unit, s.address)}>
          <div className={cn(s.icon, s.address)}>
            <Image src={placeIconPath} height={30}/>
          </div>
          <div className={s.text}>
            8 Monkville Avenue, London, United Kingdom
          </div>
        </div>

        <div className={s.unit}>
          <div className={s.icon}>
            <Image src={phoneIconPath} height={25}/>
          </div>
          <div className={s.text}>
            +7 499 681 34 18
          </div>
        </div>

        <div className={cn(s.unit, s.email)}>
          <div className={s.icon}>
            <Image src={emailIconPath} height={20}/>
          </div>
          <div className={s.text}>
            support@fondru.com
          </div>
        </div>

      </Col>
    </Row>
  );
};

export default Contacts;
