import React, {useState, useEffect, useRef, createRef, forwardRef} from "react";
import styles from './style.module.css';
import animate, {easeInOut} from "../../../utils/animate";
import getRefRect from "../../../utils/getRefRect";
import TouchableOpacity from "../../../common/core/TouchableOpacity";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import Image from "react-bootstrap/Image";
import searchIconPath from '../images/search_icon.png';
import cn from 'classnames';

const Tabs = ({tabsInfo, defaultTabIndex, slideDuration, headerRef}) => {
  const containerRef = useRef(null);
  const tabRefs = useRef(tabsInfo.map(createRef));
  const sliderRef = useRef(null);

  useEffect(() => {
    if (sliderRef.current) {
      slideToTab(defaultTabIndex);
    }
  }, [defaultTabIndex]);

  const getTabRect = (index) => {
    const {left, width} = getRefRect(tabRefs.current[index]);
    const {left:outerLeft} = getRefRect(containerRef);
    return {left: left-outerLeft, width};
  };

  const getSliderRect = () => {
    const {left, width} = getRefRect(sliderRef);
    const {left:outerLeft} = getRefRect(containerRef);
    return {left: left-outerLeft, width};
  };

  const setSliderRect = ({left, width}) => {
    if (!sliderRef.current) return;
    sliderRef.current.style.left = left + 'px';
    sliderRef.current.style.width = width + 'px';
  };

  const [activeTabIndex, setActiveTabIndex] = useState(defaultTabIndex);
  useEffect(() => {
    const setSliderStrict = () => setSliderRect(getTabRect(activeTabIndex));
    setSliderStrict();
    window.addEventListener('resize', setSliderStrict);
    window.addEventListener('load', setSliderStrict);
    return () => {
      window.removeEventListener('resize', setSliderStrict);
      window.removeEventListener('load', setSliderStrict);
    }
  }, [activeTabIndex]);

  const slidingInProgress = useRef(false);
  const onSlideStart = () => slidingInProgress.current = true;
  const onSlideEnd = () => slidingInProgress.current = false;

  const slideToTab = (index) => {
    if ((index > 0 && index < 4) || slidingInProgress.current) return;

    const {left:tabLeft, width:tabWidth} = getTabRect(index);
    const {left, width} = getSliderRect();

    onSlideStart();
    animate({
      timing: easeInOut,
      duration: slideDuration,
      draw: (progress) => {
        setSliderRect({
          left: left + (tabLeft - left)*progress,
          width: width + (tabWidth - width)*progress,
        });
      },
      then: () => {
        setSliderRect(getTabRect(index));
        onSlideEnd();
      }
    });

    setActiveTabIndex(index);
  };

  const tabs = tabsInfo.map(({name, content, action, tabClassName, dropDown}, i) => (
    <Tab
      key={`${name}${i}`}
      onClick={() => {
        slideToTab(i);
        action();
      }}
      ref={tabRefs.current[i]}
      containerRef={headerRef}
      className={cn(tabClassName)}
      dropDown={dropDown}
    >
      {content}
    </Tab>
  ));

  return (
    <Row className={styles.container}>
      <Col
        md={12}
        lg={10}
        className={styles.tabs}
        ref={containerRef}
      >
        <div
          className={styles.slider}
          ref={sliderRef}
        />
        {tabs}
        <TouchableOpacity
          className={styles.search}
        >
          <Image
            className={styles.icon}
            src={searchIconPath}
          />
        </TouchableOpacity>
      </Col>
    </Row>
  );
};

const Tab = forwardRef(({children, onClick, className, dropDown, containerRef}, ref) => {
  const [isDropDown, setDropDown] = useState(false);

  const dropDownRef = useRef(null);

  const onMouseEnter = () => {
    setDropDown(true);
  };

  const onMouseOut = ({target, relatedTarget}) => {
    if (ref.current.contains(target) && !dropDownRef.current.contains(relatedTarget)) {
      setDropDown(false);
    }
  };

  const onDropDownMouseOut = ({target, relatedTarget}) => {
    if (dropDownRef.current.contains(target) && !dropDownRef.current.contains(relatedTarget) && !ref.current.contains(relatedTarget)) {
      setDropDown(false);
    }
  };

  useEffect(() => {
    if (dropDown) {
      ref.current.addEventListener('mouseenter', onMouseEnter);
      ref.current.addEventListener('mouseout', onMouseOut);
      dropDownRef.current.addEventListener('mouseout', onDropDownMouseOut);
    }
  }, []);

  const dropDownElement = <DropDown
    ref={dropDownRef}
    info={dropDown}
    tabRef={ref}
    showing={isDropDown}
    containerRef={containerRef}
    onMouseOut={onMouseOut}
  />;

  return (
    <>
      {dropDownElement}
      <TouchableOpacity
        ref={ref}
        onClick={onClick}
        className={cn(styles.tab, className)}
      >
        {children}
      </TouchableOpacity>
    </>
  );
});

const DropDown = forwardRef(({info=[], showing, tabRef, containerRef, setMouseOverDropDown}, ref) => {
  const container = containerRef && containerRef.current && getRefRect(containerRef);
  const tab = tabRef && tabRef.current && getRefRect(tabRef);

  return (
    <div
      ref={ref}
      className={cn(styles.dropdown, showing && styles.showing)}
      style={tab && container && {left: tab.left - container.left, top: tab.bottom - container.top}}
    >
      {showing && info.map(({name, action}) => (
        <TouchableOpacity
          key={name}
          onClick={action}
          className={styles.dropItem}
        >
          {name}
        </TouchableOpacity>
      ))}
    </div>
  );
});

export default Tabs;
